body{
    background: radial-gradient(black, rgb(31, 1, 1));
}

.black {
    background-color: black;
    /* width: 100%; */
    width: 100%;
    left: 0px;

    /* margin-left: -30px; */
}

.indexItem {

    display: flex;
    justify-content: space-around;
    align-items: center;
    

    transition: all 0.1s ease-in-out;
}

.indexItem:hover {
    border: 2px solid #f7f8fa;
    transition: all 0.1s ease-in-out;
}

.indexItemText {
    font-size: 10px;
    color: #f7f8fa;

}

.circle {
    height: 10px;
    width: 10px;

    border-radius: 50%;
    border: 1px solid #f7f8fa;
}

.chevronWrapper {

    position: fixed;
    left: 50%;

    height: 100px;
    width: 100px;
    transition: all 0.1s ease-in-out;

    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

}

.chevron {
    position: fixed;
    /* left: 50%; */

    text-align: center;
    padding: 0.5px;
    margin-bottom: 1px;
    height: 1px;
    width: 150px;
    transition: all 0.1s ease-in-out;

}

.chevronWrapper:hover {
    opacity: 80%;
    transition: all 0.1s ease-in-out;
}

.chevronWrapper:has(:hover) .chevron {
    opacity: 50%;
    transition: all 0.1s ease-in-out;

}

.chevronWrapper.top,
.chevron.top {
    top: 110px;
    transform: translateX(-50%) rotate(180deg);
}

.chevronWrapper.bottom,
.chevron.bottom {
    bottom: 10px;
    transform: translateX(-50%);
}
  
.chevron:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 50%;
    background: #f7f8fa;
    -webkit-transform: skew(0deg, 6deg);
    -moz-transform: skew(0deg, 6deg);
    -ms-transform: skew(0deg, 6deg);
    -o-transform: skew(0deg, 6deg);
    transform: skew(0deg, 6deg);
}

.chevron:after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 50%;
    background: #f7f8fa;
    -webkit-transform: skew(0deg, -6deg);
    -moz-transform: skew(0deg, -6deg);
    -ms-transform: skew(0deg, -6deg);
    -o-transform: skew(0deg, -6deg);
    transform: skew(0deg, -6deg);
}


